    align-items: center;
    background-color: #131313;
    width: 50%;
    height: 100%;
    padding: 20px;
    justify-content: center;
    opacity: 0.9;
    transition: 0.3s;
    box-shadow: 0 10px 27px rgba(0, 0, 0, 0.5);
    z-index: 100;
    right: -100%;
  }
  .menu__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0;
  }
  .menu__list-item {
    margin-right: 0;
    margin-bottom: 30px;
  }
  .menu__link {
    font-size: 1.5rem;
  }
  .menu.active {
    right: 0;
  }
  .hand {
    display: none;
  }
  .br {
    display: none;
  }
}
@media (max-width: 700px) {
  .header {
    padding: 26px 20px 60px;
  }
  .header-content {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }
  .header-content__headers {
    align-self: flex-start;
  }
  .logo__link {
    width: 85px;
  }
  .header__top {
    display: grid;
    grid-template-columns: 1fr 155px 1fr;
    grid-template-rows: auto;
    gap: 37px;
  }
  .telephone {
    grid-area: 2/2;
  }
  .telephone__number {
    font-size: 1.5rem;
    text-align: center;
  }
  .location__title {
    font-size: 0.4rem;
  }
  .location__city {
    font-size: 1rem;
  }
  .header-content__title {
    font-size: 1.5rem;
    line-height: 1.3rem;
  }
  .header-content__title--big {
    font-size: 1.5rem;
    line-height: 1.85rem;
  }
  .header-content__subtitle {
    font-size: 1.5rem;
    line-height: 1.3rem;
  }
  .header-content__earnings {
    font-size: 1.3rem;
    line-height: 1.375rem;
  }
  .form__fillout {
    font-size: 1.125rem;
  }
  .form__subtitle {
    font-size: 0.85rem;
  }
  .br {
    display: none;
  }
  .burger-menu {
    top: 15px;
  }
}
@media (max-width: 425px) {
  .header__top {
    gap: 10px;
  }
  .location__title {
    font-size: 0.4rem;
  }
  .menu__list-item {
    margin-bottom: 15px;
  }
  .menu__link {
    font-size: 1rem;
  }
}